$skeletonBG: linear-gradient(90deg, rgba(165, 165, 165, 0.6702031154258579) 0%, rgba(230, 230, 230, 1) 50%, rgba(165, 165, 165, 1) 100%);


.wrapper {
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    font-family: Arial, Helvetica, sans-serif;
    width: fit-content;

}

.main {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: relative;
    background-color: gray;
}

.loadPoster {
    width: 350px;
    height: 550px;
    margin: 0 40px 0 0;
    background: $skeletonBG;
    background-size: cover;
    background-position: center;
    background-size: 200% 200%;
    animation: skeletonBG 1s ease infinite;
    border-radius: 5px;
}

.tagline {
    background-color: rgba(212, 191, 172, 0.952);
    padding: 10px;
    border: 1px rgb(132, 74, 49) solid;
    border-radius: 5px;
    color: rgb(132, 74, 49);
    font-style: italic;
}

.loadMoreInfo {
    height: 200px;
    width: 800px;
    border-radius: 5px;
    background: $skeletonBG;
    background-size: cover;
    background-position: center;
    background-size: 200% 200%;
    animation: skeletonBG 1s ease infinite;
}

.loadTagline {
    width: 800px;
    height: 30px;
    border-radius: 5px;
    background: $skeletonBG;
    background-size: cover;
    background-position: center;
    background-size: 200% 200%;
    animation: skeletonBG 1s ease infinite;
    border-radius: 5px;
}

.bgWrapper {
    width: 100%;
    position: absolute;
    height: 100%;
    overflow: hidden;
    z-index: -1;
    top: 0;
}

.background {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 100%;
    filter: blur(50px) brightness(50%);
}

.title {
    color: black;
    font-size: 50px;
    margin: 30px 0;
    line-height: 50px;
    text-transform: uppercase;
}

.loadTitle {
    margin: 30px 0;
    height: 50px;
    width: 800px;
    border-radius: 5px;
    background: $skeletonBG;
    background-size: cover;
    background-position: center;
    background-size: 200% 200%;
    animation: skeletonBG 1s ease infinite;
}

.loadDescription {
    border-radius: 5px;
    margin: 0;
    width: 800px;
    height: 120px;
    background: $skeletonBG;
    background-size: cover;
    background-position: center;
    background-size: 200% 200%;
    animation: skeletonBG 1s ease infinite;
}

.info {
    display: flex;
    background-color: #ffffffd4;
    padding: 35px;
    border-radius: 0 0 10px 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.297);
}

.releaseDate {
    margin-top: 0;
}

.voteAverage {
    font-size: 40px;
    color: rgb(208, 128, 0);
    font-weight: bold;
}

.moreInfo {
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #4A4A4A;
    max-width: 1000px;
    line-height: 30px;
}

.text {
    font-size: 25px;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 20px;
    color: #4A4A4A;
    max-width: 1000px;
    line-height: 30px;
    max-width: 800px;
}

.poster {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.genres {
    padding: 0;
    margin-top: 0;
    list-style: none;
    font-weight: bold;
    min-width: fit-content;
}

.description {
    margin: 0;
}

.similarMovies {
    margin-top: 50px;
    width: fit-content;
    margin: 70px auto;
    border-radius: 10px;
}

.posterWrapper {
    width: 350px;
    height: 550px;
    margin: 0 40px 0 0;
    overflow: hidden;
}

.homepage {
    text-decoration: none;
    color: rgb(208, 128, 0);
}

.releaseDate {
    font-weight: bold;
}


@keyframes skeletonBG {
    0% {
        background-position: 0 0%;
    }

    50% {
        background-position: 100% 100%;
    }

    100% {
        background-position: 0 0%;
    }
}